import type { BiPluginConfig } from '@ali/4ever-cangjie';
import { 
  biFactory,
  oldActions,
  oldUtils,
  createWithDraggable,
  createDraggableElement,
  SCROLLABLE_ATTRIBUTE,
  DragMenuActionType,
 } from '@ali/4ever-plugin-draggable';

export const createDraggablePlugin = (configs: BiPluginConfig) => biFactory(configs);

createDraggablePlugin.actions = oldActions;
createDraggablePlugin.utils = oldUtils;
createDraggablePlugin.createWithDraggable = createWithDraggable;
createDraggablePlugin.createDraggableElement = createDraggableElement;
createDraggablePlugin.scrollableAttribute = SCROLLABLE_ATTRIBUTE;
createDraggablePlugin.dragMenuActionType = DragMenuActionType;

export default createDraggablePlugin;
